import { Component, OnInit } from '@angular/core';

import { CatalogSandbox } from '@sandboxes/catalog.sandbox';
import { ICatalog } from '@store/models/catalog.model';
import { UserSandbox } from '@sandboxes/user.sandbox';
import { Router } from '@angular/router';
import { IUser, UserTypeEnum } from '@store/models/user.model';

@Component({
  selector: 'app-catalogs',
  templateUrl: './catalogs-page.component.html',
  styleUrls: ['./catalogs-page.component.scss']
})
export class CatalogsPage  implements OnInit {
  public catalogs: ICatalog[];
  public name: string;
  public isMerchant = false;

  constructor(
      private catalogSandbox: CatalogSandbox,
      private userSandbox: UserSandbox,
      private router: Router,
  ) {
  }

  ngOnInit() {
    this.catalogSandbox.getCatalogs();

    this.catalogSandbox.catalogErrorMessage$.subscribe((error: string) => {
      // TODO: show error
    });
    this.catalogSandbox.catalogs$.subscribe((catalogs: ICatalog[]) => {
      this.catalogs = catalogs;
    });
    this.userSandbox.user$.subscribe((user: IUser) => {
      this.name = user.companyName;
      this.isMerchant = user.role === UserTypeEnum.MERCHANT;
    });
  }

  onSelectCatalog(catalog) {
    this.catalogSandbox.setCurrentCatalog(catalog);
    this.router.navigateByUrl(`/app/tabs/catalogs/items`);
  }

  onNewItem() {
    this.router.navigateByUrl(`/app/tabs/catalogs/editItem`);
  }

  onNewCatalog() {
    this.router.navigateByUrl(`/app/tabs/catalogs/newCatalog`);
  }

  onShowProducts() {
    this.router.navigate(['items']);
  }

}
